.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}

.App-header {
  background-image: url(https://i.gifer.com/YmXB.gif);
  background-size: 100%;
  background-color: #020b1d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  0% {
    width: 20%;
    height: 20%;
}
  50% {
    width: 30%;
    height: 30%;
  }
  100% {
    width: 20%;
    height: 20%;
  }
}
